.profilebg {
    background-color: var(--neutral);
    background-size: cover;
    position: relative;
    border-radius: 2px 2px 0 0;
    z-index: 0;
}
.profile .profilebg-blue {
    background-color: #70ADAE;
    background-image: url('/img/profiles/profile-bg-blue.jpg');
}
.profile .profilebg-green {
    background-color: #8FA22C;
    background-image: url('/img/profiles/profile-bg-green.jpg');
}
.profile .profilebg-greendk {
    background-color: #78805B;
    background-image: url('/img/profiles/profile-bg-greendk.jpg');
}
.profile .profilebg-indigo {
    background-color: #746665;
    background-image: url('/img/profiles/profile-bg-indigo.jpg');
}
.profile .profilebg-orange {
    background-color: #EEA82F;
    background-image: url('/img/profiles/profile-bg-orange.jpg');
}
.profile .profilebg-peach {
    background-color: #E37D67;
    background-image: url('/img/profiles/profile-bg-peach.jpg');
}
.profile .profilebg-pink {
    background-color: #F07E98;
    background-image: url('/img/profiles/profile-bg-pink.jpg');
}
.profile .profilebg-purple {
    background-color: #B375B4;
    background-image: url('/img/profiles/profile-bg-purple.jpg');
}
.profilebg .profiletxt,
.profilebg .profiletxt a {
    color: var(--white);
    text-shadow: 0 1px 1px rgba(0,0,0,.3);
}
.profileimg img { border: 1px solid rgba(0, 153, 255, .6); }

@media not all and (--small-screen) {
    .profile-large .ratings { margin-right: 20px; }
}
.profile-large .profileimg img,
.profile-medium .profileimg img {
    max-width: 100%;
    max-height: 150px;
}
.profile-small .profileimg img { max-width: 100px; }
.profile-xsmall .profileimg img { max-width: 65px; }
.profile-company-small .profileimg img {
    max-width: 200px;
    max-height: 200px;
}

.profileimg .verified:after {
    content: '';
    display: inline-block;
    background-image: url('data:image/svg+xml;base64,');
    background-size: contain;
    position: absolute;
    left: 50%;
    bottom: 5px;
}

.profile-medium .profileimg .verified:after {
    height: 30px;
    width: 30px;
    margin-left: 30px;
}

.profile-small .profileimg .verified:after {
    height: 20px;
    width: 20px;
    margin-left: 20px;
}

.profile-xsmall .profileimg .verified:after {
    height: 13px;
    width: 13px;
    margin-left: 13px;
}

.profiletxt .verified:after {
    content: '';
    display: inline-block;
    height: 20px;
    width: 20px;
    margin-left: 5px;
    background-image: url('data:image/svg+xml;base64,');
    background-size: contain;
}
.companyprofile .profilebg {
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.companyprofile .profilebg:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
    background-image: linear-gradient(to top, rgba(4,37,60,.4), rgba(4,37,60,0) 60%);
}
.companyprofile .keyinfo { background-color: rgba(255, 255, 255, .9); }

.editmode .profilebg { position: relative; }
.editmode .profilebg:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.7);
}
.editmode .profilebg .editbg {
    position: relative;
    z-index: 1;
}
.editmode .profilebg .saveprofile {
    position: absolute;
    z-index: 1;
    bottom: 20px;
    right: 20px;
}
.editmode .profileimg .profileimg-overlay {
    display: inline-block;
    position: relative;
    z-index: 1;
}
.editmode .profileimg .profileimg-overlay .editimg {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.7);
    border: 3px solid rgba(255,255,255,.5);
    z-index: 3;
}
.editmode .profiletxt div { z-index: 3; }
.companyprofile .profileimg img {
    max-height: 120px;
    background-color: var(--white);
    border: 5px solid var(--white);
}
@media not all and (--small-screen) {
    .companyprofile .profilebg { padding-top: 100px; }
    .editmode .profilebg .editbg {
        position: absolute;
        z-index: 1;
        top: 10px;
        left: 40%;
    }
}
@media (--small-screen) {
    .companyprofile .profileimg img { max-width: 200px; }
}
/* logo formatting */
.company-logo-container {
    overflow: hidden;
    position: relative;
    padding-top: 45%;
}
.company-logo {
    max-height: 100%;
    max-width: 65%;
}
