@import '../../config/variables.css';
/* **************** TEMPLATE ***************** */
/* ====== Page Head, Body, and Foot ====== */
.main {
    display: table-cell;
    *display: block;
    width: auto;
}
body, html { background-color: var(--blue-greyish); }
.body, .main { *zoom: 1; }
.body:after, .main:after {
    clear: both;
    display: block;
    visibility: hidden;
    overflow: hidden;
    height: 0 !important;
    line-height: 0;
    font-size: xx-large;
    content: ' x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x ';
}
.page {
    width: auto;
    _text-align: left;
    margin-left: 2px;
}
.topbar.fixed + .container,
.topmenu.fixed + .container,
.topbar.fixed + .topbar-level2.hidden + .container,
.topmenu.fixed + .topbar-level2.hidden + .container { margin-top: 65px; }

.topmenu.fixed + .topbar-level2.fixed:not(.hidden) + .container { margin-top: 110px; }

/* Special case. Topmenu margin comes from finnlet */
.topbar-level2.fixed:not(.hidden) + .container { margin-top: 45px; }

.container.fullsize { max-width: 100%; }
/* wraps other template elems to set width */ /* text-align IE5.5 */
