.head {
    position: relative;
    z-index: 10;
}

@media not all and (--small-screen) {
    .page {
        max-width: 990px;
        margin-left: 10px;
    }
    .container {
        max-width: 1010px;
        margin: 0 auto;
    }
    .container.kindawide { max-width: 1240px; }
    .container.prettywide { max-width: 1470px; }

    .skyskraper-left {
        position: absolute;
        left: -240px;
        top: 0;
    }
    .skyskraper-right {
        position: absolute;
        right: -240px;
        top: 0;
    }
}
@media (min-width: 990px) {
    .page { margin-left: 15px; }
}
