.speech-byline {
    margin-left: 68px;
    position: relative;
    z-index: 2;
}
.speech {
    position: relative;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    border-radius: 5px;
}
.speech:after {
    bottom: -17px;
    content: '';
    left: 21px;
    position: absolute;
    width: 0;
    height: 0;
    border-top: 18px solid;
    border-left: 18px solid transparent;
    border-right: 0;
    transform: skew(28deg);
    z-index: 1;
}
/* Legacy colors */
.speech.bg-lt-gray:after { border-top-color: var(--lt-gray); }
.speech.bg-black:after { border-top-color: var(--black); }
.speech.bg-dk-gray:after { border-top-color: var(--dk-gray); }
.speech.bg-pale-gray:after { border-top-color: var(--pale-gray); }
.speech.bg-white:after { border-top-color: var(--white); }
.speech.bg-dkdk-blue:after { border-top-color: var(--dkdk-blue); }
.speech.bg-dk-blue:after { border-top-color: var(--blue); }
.speech.bg-lt-blue:after { border-top-color: var(--lt-blue); }
